.nav-title {
  display: flex;
  justify-content: flex-start;
  align-items: flex-start;
  width: 500px;

  padding-bottom: 30px;

  font-size: 70px;
  line-height: 0.75;
  text-align: start;
  color: #543f2e;
}

.nav-links {
  width: 70%;
  display: flex;
  justify-content: flex-end;
  align-items: flex-start;

  
}


.nav-link {
  line-height: 0.8;
  font-size: 55px;
  color: #543f2e;

  border-top-style: solid;
  border-width: 6px;
  
  padding: 0 1px 0 1px;
  margin-left: 25px;
}

.nav-link:hover {
  background-color: #543f2e;
  border-color: #543f2e;
  color: #ebe5d9;
  height: 180px;

  line-height: 1;

  display: flex;
  align-items: flex-end;

  transition: 0.2s;
}



