.lds-roller {
  display: inline-block;
  position: relative;
  width: 80px;
  height: 80px;
}
.lds-roller div {
  animation: lds-roller 1.2s cubic-bezier(0.5, 0, 0.5, 1) infinite;
  transform-origin: 40px 40px;
}
.lds-roller div:after {
  content: " ";
  display: block;
  position: absolute;
  width: 7px;
  height: 7px;
  border-radius: 50%;
  background: #543f2e;
  margin: -4px 0 0 -4px;
}
.lds-roller div:nth-child(1) {
  animation-delay: -0.036s;
}
.lds-roller div:nth-child(1):after {
  top: 63px;
  left: 63px;
}
.lds-roller div:nth-child(2) {
  animation-delay: -0.072s;
}
.lds-roller div:nth-child(2):after {
  top: 68px;
  left: 56px;
}
.lds-roller div:nth-child(3) {
  animation-delay: -0.108s;
}
.lds-roller div:nth-child(3):after {
  top: 71px;
  left: 48px;
}
.lds-roller div:nth-child(4) {
  animation-delay: -0.144s;
}
.lds-roller div:nth-child(4):after {
  top: 72px;
  left: 40px;
}
.lds-roller div:nth-child(5) {
  animation-delay: -0.18s;
}
.lds-roller div:nth-child(5):after {
  top: 71px;
  left: 32px;
}
.lds-roller div:nth-child(6) {
  animation-delay: -0.216s;
}
.lds-roller div:nth-child(6):after {
  top: 68px;
  left: 24px;
}
.lds-roller div:nth-child(7) {
  animation-delay: -0.252s;
}
.lds-roller div:nth-child(7):after {
  top: 63px;
  left: 17px;
}
.lds-roller div:nth-child(8) {
  animation-delay: -0.288s;
}
.lds-roller div:nth-child(8):after {
  top: 56px;
  left: 12px;
}
@keyframes lds-roller {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}
.boosterPage {
  display: flex;
  flex-direction: row;
  overflow-x: auto;

  /* height: 100vh; */

  background-color: var(--main-bg-color);

  padding-left: 30px;
  padding-top: 5px;
  padding-bottom: 50px;
}

.boosterPage::-webkit-scrollbar {
  display: none;
}


.boosterPage-core {
  display: flex;
  flex-direction: column;
   align-items: center;

  margin-left: 5px;
}

/* ===================================================== */
.boosterPage-status {
  display: flex;
  align-items: center;
  justify-content: center;
  /* border-style: bottom solid black; */
  color: var(--main-bg-color);
  font-size: 17px;

  border-radius: 5px;

  width: 50px;
  min-height: 25px;
}

.boosterPage-status-active {
  background-color: rgb(94, 139, 94);
}
.boosterPage-status-lost {
  background-color: rgb(85, 83, 83);
}
.boosterPage-status-unknown {
  background-color: rgb(169, 204, 168);
}
.boosterPage-status-expended {
  background-color: rgb(139, 129, 129);
}
.boosterPage-status-inactive {
  background-color: rgb(212, 120, 120);
}


/* ===================================================== */
.boosterPage-name {
  color: var(--main-font-color);
  padding-top: 5px;
  font-weight: bold;
  text-align: center;
  line-height: 1.2;
  min-height: 40px;
  display: flex;
  align-items: center;
  justify-content: center;
  text-wrap: balance;
}


/* ===================================================== */
.boosterPage-launchBlock {
  display: flex;
  align-items: center;
  justify-content: center;

  color: white;
  font-size: 24px;
  line-height: 1;
  padding: 2px;
  box-sizing: border-box;

  height: 45px;
  width: 45px;
  border-radius: 5px;
  margin-top: 5px;
  border: 2px solid transparent;
}

.boosterPage-launchBlock-upcoming {
  background-color: gray;
}
.boosterPage-launchBlock-noattempt {
  background-color: rgb(70, 66, 66);
}
.boosterPage-launchBlock-fail {
  background-color: rgb(231, 136, 136);
}
.boosterPage-launchBlock-ocean {
  background-color: rgb(197, 157, 82);
}
.boosterPage-launchBlock-RTLS {
  background-color: rgb(147, 199, 147);
}
.boosterPage-launchBlock-ASDS {
  background-color: rgb(173, 203, 221);
}




/* ===================================================== */
.boosterPage-launchBlock {
  position: relative;
}

.boosterPage-launchBlock:hover {
  border-color: black;
}

.boosterPage-hoverBox {
  position: fixed;
  z-index: 1;

  visibility: hidden;

  width: 120px;
  height: 120px;

  background-color: rgb(209, 187, 209);

  border-radius: 10px;

}

.boosterPage-launchBlock:hover .boosterPage-hoverBox {
  visibility: visible;
}

.boosterPage-hoverBox-imgDiv {
  margin-top: -20px;
}
.boosterPage-hoverBox-img {
  width: 50px;
  height: 50px;

}


.boosterPage-hoverBox-time {
  font-size: 20px;
  font-weight: bold;

  /* padding-top: 10px; */

  color: var(--main-font-color);
}

.boosterPage-hoverBox-name {
  font-size: 15px;
  font-weight: bold;

  padding-top: 5px;
  height: 80px;

  color: black;
}
.boosterPage-hoverBox-link {
  text-decoration: none;
  color: black;
}



/* ===================================================== */
.boosterPage-count {
  color: var(--main-font-color);
  padding-top: 2px;
  padding-bottom: 5px;
  font-weight: bold;
}


/* ===================================================== */
.legend {
  display: flex;
  align-items: flex-start;
  flex-direction: column;


}

.legend-block-line {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  /* justify-content: center; */

  padding-bottom: 5px;
}

.legend-block-unit {
  display: flex;
  align-items: center;
  padding-top: 2px;
}

.legend-toggle-container {
  display: flex;
  align-items: center;
  margin-left: 10px;
}

.legend-toggle-checkbox {
  display: none;
}

.legend-toggle-label {
  position: relative;
  display: inline-block;
  width: 50px;
  height: 25px;
  background-color: rgb(139, 129, 129);
  border-radius: 5px;
  transition: .4s;
  cursor: pointer;
  margin-right: 8px;
}

.legend-toggle-label:before {
  position: absolute;
  content: "";
  height: 19px;
  width: 19px;
  left: 3px;
  bottom: 3px;
  background-color: white;
  border-radius: 3px;
  transition: .4s;
}

.legend-toggle-checkbox:checked + .legend-toggle-label {
  background-color: rgb(94, 139, 94);
}

.legend-toggle-checkbox:checked + .legend-toggle-label:before {
  transform: translateX(25px);
}

.legend-toggle-text {
  font-size: 14px;
  font-weight: bold;
  color: var(--main-font-color);
  cursor: pointer;
}

.turnaround-display {
  width: 45px;
  height: 25px;
  border-radius: 5px;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 12px;
  font-weight: bold;
  background-color: transparent;
  border-style: solid;
  border-width: 3px;
  box-sizing: border-box;
  margin-top: 5px;
  margin-bottom: 5px;
}

.legend-singleBlock {
  margin-right: 5px;
  margin-left: 5px;
}

.legend-statusBlock {
  width: 30px;
  height: 30px;
  border-radius: 5px;
}


/* ===================================================== */
.boosterPage-scrollButtons {
  display: flex;
  justify-content: space-between;

  padding-top: 3px;
}

